main {
    position: relative;
    height: 100%;
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.movie {
    // width: 25%;
    width: 274px;
    height: 439px;
    margin: 31px 20px;
    position: relative;
    .movie_title {
        font-weight: 500;
        font-size: 12px;
        text-transform: uppercase;
        margin-top: 10px;
        margin-bottom: 5px;
    }
    .genres_name {
        margin: 0;
        color: #ff6b08;
        font-weight: 500;
        font-size: 12px;
    }
    .img_container {
        width: 100%;
        height: 398px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px;
        }
    }
}